export default {
	/**
	 * -
	 * @param {Array} urls - 
	 * @param {Number} current - 
	 */
	previewImage(urls = [], current = 0) {
		uni.previewImage({
			urls: urls,
			current: current,
			indicator: 'default',
			loop: true,
			fail(err) {
				console.log('previewImage', urls, err)
			},
		})
	},


	/**
	 * 
	 * @param {String<Number>} phoneNumber - 
	 */
	callPhone(phoneNumber = '') {
		let num = phoneNumber.toString()
		uni.makePhoneCall({
			phoneNumber: num,
			fail(err) {
				console.log('makePhoneCall', err)
			},
		});
	},

}
